/**
 * Generates the Srcset for a given image.
 * @function
 * @param image - The image to generate the Srcset for.
 * @returns A promise that resolves to the Srcset for the image.
 */
async function generateSrcset(image: HeroImageBase): Promise<string> {
  return Object.entries(image.widths).map(([width, src]) => {
      return `${src} ${width}w`
    }).join(", ")
}

let heroImages: { [key: string]: HeroImage } = {}

/**
 * Initializes the hero images.
 * @function
 * @returns A promise that resolves when the hero images have been initialized.
 */
async function initializeHeroImages() {
  try {
    const entries = await Promise.all(
      Object.entries(heroImagesBase).map(async ([key, value]) => {
        const srcset = await generateSrcset(value)
        return [
          key,
          {
            parent: value.parent,
            widths: value.widths,
            srcset: (srcset && typeof srcset === "string") ? srcset : ""
          }
        ]
      })
    )
    heroImages = Object.fromEntries(entries)
  } catch (error) {
    logger.error("Error initializing heroImages:", error)
    throw error
  }
}

// Call this function during your application initialization
initializeHeroImages().then(() => { logger.info("Hero images initialized") }).catch((error: Error) => { logger.error("Error initializing hero images:", error) })

export { heroImages }
